
.lyrics-container {
  position: relative;
  height: 100%;
  color: rgba(225, 225, 225, 0.8);
  text-align: center;
}
.lyrics-container .mask {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.35);
}
.lyrics-container .backImg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    -webkit-filter: blur(90px);
            filter: blur(90px);
    opacity: 0.6;
}
.lyrics-container .cover {
    position: relative;
    z-index: 1;
}
.lyrics-container .cover img {
      width: 186px;
      height: 186px;
}
.lyrics-container .lyrics-content-wrapper {
    position: absolute;
    width: 100%;
    top: 216px;
    bottom: 20px;
    font-size: 16px;
    line-height: 34px;
    overflow: hidden;
}
.lyrics-container .lyrics-content-wrapper .lyrics-content {
      cursor: default;
}
.lyrics-container .lyrics-content-wrapper .lyrics-content .on {
        color: #31c27c;
}
.lyrics-container .lyrics-content-wrapper .loading {
      position: absolute;
      top: 0px;
      bottom: 0px;
      width: 100%;
      height: 100%;
      font-size: 16px;
}



