
body,
html {
  height: 100%;
  overflow: hidden;
  background: #42474c;
}
body .iScrollVerticalScrollbar,
  html .iScrollVerticalScrollbar {
    position: absolute;
    z-index: 9999;
    width: 7px;
    bottom: 10px;
    top: 10px;
    right: 0px;
    pointer-events: none;
    background: rgba(255, 255, 255, 0.1);
}
body .iScrollVerticalScrollbar .iScrollIndicator,
    html .iScrollVerticalScrollbar .iScrollIndicator {
      box-sizing: border-box;
      position: absolute;
      border-radius: 3px;
      width: 100%;
      transition-duration: 0ms;
      display: block;
      transform: translate(0px, 0px) translateZ(0px);
      transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
      background: rgba(255, 255, 255, 0.1);
}
body .app,
  html .app {
    height: 100%;
}
body .app .main-content,
    html .app .main-content {
      position: absolute;
      top: 80px;
      bottom: 165px;
      left: 0;
      right: 0;
      padding-top: 100px;
      width: 1100px;
      margin: auto;
      font-size: 0;
}
body .app .main-content .search-container,
      html .app .main-content .search-container {
        position: absolute;
        width: 100%;
        margin-top: -100px;
        text-align: center;
        height: 70px;
        z-index: 1;
}
body .app .main-content .search-container .search-wrapper,
        html .app .main-content .search-container .search-wrapper {
          position: relative;
          display: inline-block;
          box-sizing: border-box;
          height: 50px;
          width: 700px;
          padding-left: 10px;
          padding-right: 50px;
          background: rgba(255, 255, 255, 0.07);
          transition: 0.15s;
}
body .app .main-content .search-container .search-wrapper .search,
          html .app .main-content .search-container .search-wrapper .search {
            width: 100%;
            height: 100%;
            color: rgba(225, 225, 225, 0.8);
            font-size: 16px;
            background: transparent;
            border: none;
            outline: none;
}
body .app .main-content .search-container .search-wrapper .search::-moz-placeholder,
            html .app .main-content .search-container .search-wrapper .search::-moz-placeholder {
              color: rgba(225, 225, 225, 0.8);
}
body .app .main-content .search-container .search-wrapper .search::-webkit-input-placeholder,
            html .app .main-content .search-container .search-wrapper .search::-webkit-input-placeholder {
              color: rgba(225, 225, 225, 0.8);
}
body .app .main-content .search-container .search-wrapper .search:-ms-input-placeholder,
            html .app .main-content .search-container .search-wrapper .search:-ms-input-placeholder {
              color: rgba(225, 225, 225, 0.8);
}
body .app .main-content .search-container .search-wrapper .search-btn,
          html .app .main-content .search-container .search-wrapper .search-btn {
            position: absolute;
            top: 0;
            right: 0;
            width: 50px;
            height: 50px;
            background: transparent;
            border: none;
            padding: 0;
            cursor: pointer;
            outline: none;
}
body .app .main-content .search-container .search-wrapper .search-btn:hover,
            html .app .main-content .search-container .search-wrapper .search-btn:hover {
              background: rgba(255, 255, 255, 0.1);
}
body .app .main-content .search-container .search-wrapper .search-btn .search-icon,
            html .app .main-content .search-container .search-wrapper .search-btn .search-icon {
              display: inline-block;
              width: 16px;
              height: 16px;
              background: url("http://7xrkxs.com1.z0.glb.clouddn.com/music/icon_sprite.png");
              background-position: 0 -40px;
}
body .app .main-content .list-wrapper,
      html .app .main-content .list-wrapper {
        position: relative;
        display: inline-block;
        padding-right: 25px;
        width: 700px;
        height: 100%;
        overflow: hidden;
        z-index: 1;
}
body .app .main-content .lyrics-wrapper,
      html .app .main-content .lyrics-wrapper {
        vertical-align: top;
        margin-left: 35px;
        width: 340px;
        display: inline-block;
        height: 100%;
}
body .app .player-wrapper,
    html .app .player-wrapper {
      position: fixed;
      bottom: 20px;
      width: 100%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
}



