
.player-container {
  width: 1100px;
  height: 115px;
  font-size: 0;
}
.player-container .left-content {
    display: inline-block;
    text-align: center;
    vertical-align: top;
    width: 200px;
    height: 100%;
    line-height: 115px;
    font-size: 0;
}
.player-container .left-content .bg-icon {
      vertical-align: middle;
      display: inline-block;
      opacity: 0.8;
      cursor: pointer;
      background-image: url("http://7xrkxs.com1.z0.glb.clouddn.com/music/player.png");
}
.player-container .left-content .bg-icon:hover {
      opacity: 1;
}
.player-container .left-content .bg-icon.prev {
      width: 19px;
      height: 20px;
      margin-right: 65px;
      background-position: 0 -30px;
}
.player-container .left-content .bg-icon.play {
      width: 21px;
      height: 29px;
      margin-right: 60px;
      background-position: 0 0;
}
.player-container .left-content .bg-icon.pause {
      width: 21px;
      height: 29px;
      margin-right: 60px;
      background-position: -30px 0;
}
.player-container .left-content .bg-icon.next {
      width: 19px;
      height: 20px;
      background-position: 0 -52px;
}
.player-container .right-content {
    display: inline-block;
    vertical-align: top;
    width: 850px;
    height: 100%;
    margin-left: 50px;
}
.player-container .right-content .intro {
      margin-top: 20px;
      font-size: 20px;
}
.player-container .right-content .intro .title {
        color: rgba(225, 225, 225, 0.8);
}
.player-container .right-content .intro .singer {
        margin-left: 10px;
        color: rgba(225, 225, 225, 0.8);
        font-size: 20px;
}
.player-container .right-content .progress-wrapper {
      position: relative;
      height: 25px;
      line-height: 25px;
      margin-top: 13px;
      color: rgba(225, 225, 225, 0.8);
}
.player-container .right-content .progress-wrapper .time {
        font-size: 16px;
        display: inline-block;
        vertical-align: top;
}
.player-container .right-content .progress-wrapper .volume-wrapper {
        width: 130px;
        display: inline-block;
        margin-left: 16px;
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-btn {
          display: inline-block;
          width: 26px;
          height: 21px;
          margin-top: 1px;
          background: url("http://7xrkxs.com1.z0.glb.clouddn.com/music/player.png");
          opacity: 0.8;
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-on {
          background-position: 0 -144px;
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-off {
          background-position: 0 -182px;
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-btn:hover {
          opacity: 1;
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-bar {
          position: absolute;
          display: inline-block;
          margin-top: 10px;
          margin-left: 10px;
          width: 80px;
          height: 2px;
          background: rgba(225, 225, 225, 0.1);
          cursor: pointer;
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-bar .current-bar {
            position: absolute;
            left: 0;
            top: 0;
            height: 2px;
            background: rgba(225, 225, 225, 0.7);
}
.player-container .right-content .progress-wrapper .volume-wrapper .volume-bar .controller {
            position: absolute;
            top: 0;
            margin-left: -5px;
            margin-top: -4px;
            width: 10px;
            height: 10px;
            background: rgba(225, 225, 225, 0.9);
            border-radius: 50%;
            cursor: pointer;
}
.player-container .right-content .progress-wrapper .play_mod-icon {
        position: absolute;
        display: inline-block;
        right: 0;
        top: 0;
        background: url("http://7xrkxs.com1.z0.glb.clouddn.com/music/player.png");
        opacity: 0.8;
        cursor: pointer;
}
.player-container .right-content .progress-wrapper .play_mod-icon.order {
          width: 26px;
          height: 25px;
          background-position: 0 -205px;
}
.player-container .right-content .progress-wrapper .play_mod-icon.random {
          width: 25px;
          height: 19px;
          background-position: 0 -74px;
}
.player-container .right-content .progress-wrapper .play_mod-icon.cycle {
          width: 26px;
          height: 25px;
          background-position: 0 -232px;
}
.player-container .right-content .progress-wrapper .play_mod-icon:hover {
        opacity: 1;
}
.player-container .right-content .progress-wrapper .progress_bar-wrapper {
        position: absolute;
        bottom: -15px;
        width: 100%;
}
.player-container .right-content .progress-wrapper .progress_bar-wrapper .progress_bar {
          position: relative;
          height: 2px;
          background: rgba(225, 225, 225, 0.2);
          cursor: pointer;
          overflow: hidden;
}
.player-container .right-content .progress-wrapper .progress_bar-wrapper .progress_bar .current-progress {
            position: absolute;
            height: 2px;
            background: rgba(225, 225, 225, 0.7);
}
.player-container .right-content .progress-wrapper .progress_bar-wrapper .progress-btn {
          position: absolute;
          width: 10px;
          height: 10px;
          background: rgba(225, 225, 225, 0.9);
          border-radius: 50%;
          margin-top: -6px;
          margin-left: -5px;
          cursor: pointer;
}



