
.list-container {
  color: rgba(225, 225, 225, 0.8);
}
.list-container .song-list {
    font-size: 14px;
}
.list-container .song-list .item {
      height: 50px;
      line-height: 50px;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(225, 225, 225, 0.1);
      cursor: default;
      overflow: hidden;
}
.list-container .song-list .item.playing {
        color: #fff;
}
.list-container .song-list .item.playing .song_num-wrapper {
          font-size: 0;
          background: url("http://7xrkxs.com1.z0.glb.clouddn.com/music/wave.gif") 25px 18px no-repeat;
}
.list-container .song-list .item.playing:hover .play_btn-wrapper .play-btn {
        background-position: 0px -200px;
}
.list-container .song-list .item.playing .play_btn-wrapper .play-btn:hover {
        background-position: -120px -200px;
}
.list-container .song-list .item.text-center {
        text-align: center;
}
.list-container .song-list .item.load-more {
        cursor: pointer;
}
.list-container .song-list .item:hover {
        background: rgba(225, 225, 225, 0.1);
}
.list-container .song-list .item:hover .play_btn-wrapper .play-btn {
        background-position: 0 0px;
}
.list-container .song-list .item:first-child:hover {
        background: transparent;
}
.list-container .song-list .item .inline-block {
        vertical-align: top;
        display: inline-block;
        height: 50px;
        line-height: 50px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
}
.list-container .song-list .item .song_num-wrapper {
        width: 36px;
        text-align: right;
}
.list-container .song-list .item .song_name-wrapper {
        width: 304px;
        padding-left: 10px;
        box-sizing: border-box;
}
.list-container .song-list .item .play_btn-wrapper {
        width: 50px;
}
.list-container .song-list .item .play_btn-wrapper .play-btn {
          display: inline-block;
          vertical-align: middle;
          width: 36px;
          height: 36px;
          background: url("http://7xrkxs.com1.z0.glb.clouddn.com/music/icon_list_menu.png") no-repeat;
          background-position: 40px 0px;
          cursor: pointer;
}
.list-container .song-list .item .play_btn-wrapper .play-btn:hover {
          background-position: -120px 0;
}
.list-container .song-list .item .singer-wrapper {
        position: relative;
        width: 80px;
}
.list-container .song-list .item .ablum-wrapper {
        width: 150px;
        margin-left: 50px;
}



